@import './../../assets/scss/variaveis.scss';

.about {
    background-color: $color-1;
    border-bottom: 5px solid $color-3;
    width: 100%;
    overflow: hidden;
    height: 600px;
    padding: 100px 0 0;
    position: relative;


    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-direction: column;

    .about-info-1 {
        top: 10%;

        position: absolute;
        right: 15%;
        z-index: 20;
        width: 50px;
        height: 50px;
        
        &--button {
            padding-right: 5px;
            &__arrow {
                order: 3;
                display: flex;
                width: auto;
                height: auto;
            }
        }

        &.active {
            .about-info-1--button {
                &::before {
                    border-color: $color-3;
                    height: 44px;
                    width: 44px;
                    top: 0px;
                    left: 0px;
                }
            }
        }
    }

    .about-info-2 {
        position: absolute;
        top: 55%;
        right: 40%;
        z-index: 20;
        width: 50px;
        height: 50px;
        
        &--button {
            padding-left: 5px;
            &__arrow {
                order: 3;
                display: flex;
                width: auto;
                height: auto;
            }
        }

        &.active {
            .about-info-2--button {
                &::before {
                    border-color: $color-3;
                    height: 44px;
                    width: 44px;
                    top: 0px;
                    left: 0px;
                }
            }
        }
    }

    .about-info-1--button, .about-info-2--button {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%;
        cursor: pointer;

        &__arrow {
            z-index: 25;
            max-width: 25px;
            height: auto;
            transform: scale(.9);
            transition: 200ms all ease-in-out;
        }

        &:hover {
            &::before {
                border-color: $color-3;
                height: 44px;
                width: 44px;
                top: 0px;
                left: 0px;
            }

            .about-info-1--button__arrow, .about-info-2--button__arrow {
                transform: scale(1);
            }
        }

        &::before {
            content: '';
            background-color: transparent;
            border: 3px solid transparent;
            border-radius: 50%;
            height: 40px;
            width: 40px;
            left: 2px;
            position: absolute;
            top: 2px;
            z-index: 10;
            transition: 200ms all ease-in-out;
        }

        &::after {
            background-color: $branco;
            width: 42px;
            height: 42px;
            position: absolute;
            border-radius: 50%;
            z-index: 10;
            order: 2;
            left: 4px;
            top: 4px;
        }
    }

    .content {
        position: relative;
    }


    &-text {

        height: 100%;
        width: 30%;
        
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;

        h2 {
            color: $text-color-1;
            font-size: 82px;
        }
        p {
            color: $text-color-1;
            text-align: left;
            transition: 200ms all ease-in-out;

            &:nth-child(n+2) {
                display: none;
            }
        }
    }

    .info-1 {
        p {
            &:first-child,&:last-child {
                display: none;
            }
            &:nth-child(5n+2) {
                display: block;
            }
        }
    }
    .info-2 {
        p {
            &:first-child,&:nth-child(5n+2) {
                display: none;
            }
            &:last-child {
                display: block;
            }
        }
    }

    &-images {
        position: absolute;
        left: 0px;
        top: 0px;
        width: 100%;
        height: 100%;

        .lapa {
            width: 70%;
            left: 45%;
            bottom: 0%;
            position: absolute;
            z-index: 10;
        }

        .birds {
            width: 30%;
            left: 70%;
            bottom: 0%;
            position: absolute;
        }
    }
}